const SideButtons = () => {
  const buttons = [
    {
      icon: <FaWhatsapp />,
      label: "WhatsApp",
      color: "#25D366",
      link: "https://wa.me/911242577777",
    },
    {
      icon: <FaMapMarkerAlt />,
      label: "Locate Us",
      color: "#2e3192",
      link: "https://maps.app.goo.gl/mE2vAiEckM6yECxK8",
    },
    {
      icon: <FaRegCommentDots />,
      label: "Raise Complaint",
      color: "#dc3545",
      link: "/raise-complaint",
    },
  ];

  return (
    <div
      className="position-fixed d-flex flex-column align-items-center gap-3 justify-content-center"
      style={{
        right: "20px",
        top: "50%",
        transform: "translateY(-50%)",
        zIndex: 1050,
      }}
    >
      {buttons.map((btn, idx) => (
        <Link
          key={idx}
          to={btn.link}
          target="_blank"
          rel="noreferrer"
          style={{ textDecoration: "none" }}
        >
          <div
            className="side-btn text-white d-flex align-items-center justify-content-center flex-column"
            style={{
              backgroundColor: btn.color,
              width: "55px",
              height: "55px",
              borderRadius: "50%",
              transition: "all 0.3s ease",
              overflow: "hidden",
              cursor: "pointer",
              boxShadow: "0 3px 10px rgba(0,0,0,0.2)",
              position: "relative",
            }}
            onMouseEnter={(e) => {
              e.currentTarget.style.height = "190px";
              e.currentTarget.style.borderRadius = "40px";
              e.currentTarget.querySelector(".label").style.opacity = "1";
              e.currentTarget.querySelector(".label").style.bottom = "15px";
            }}
            onMouseLeave={(e) => {
              e.currentTarget.style.height = "55px";
              e.currentTarget.style.borderRadius = "50%";
              e.currentTarget.querySelector(".label").style.opacity = "0";
              e.currentTarget.querySelector(".label").style.bottom = "0";
            }}
          >
            {/* icon stays centered using absolute positioning */}
            <span
              className="fs-4"
              style={{
                position: "absolute",
                top: "50%",
                transform: "translateY(-50%)",
              }}
            >
              {btn.icon}
            </span>

            {/* label slides up on hover */}
            <span
              className="label"
              style={{
                position: "absolute",
                bottom: "0",
                opacity: 0,
                transition: "all 0.3s ease",
                fontSize: "0.75rem",
              }}
            >
              {btn.label}
            </span>
          </div>
        </Link>
      ))}

      <style>
        {`
          .side-btn:hover {
            box-shadow: 0 6px 15px rgba(0,0,0,0.25);
            transform: translateY(-2px);
          }
        `}
      </style>
    </div>
  );
};

const ScrollToTop = () => {
  const [visible, setVisible] = useState(false);

  useEffect(() => {
    const onScroll = () => setVisible(window.scrollY > 300);
    window.addEventListener("scroll", onScroll);
    return () => window.removeEventListener("scroll", onScroll);
  }, []);

  const scrollToTop = () => {
    window.scrollTo({ top: 0, behavior: "smooth" });
  };

  if (!visible) return null;

  return (
    <button
      onClick={scrollToTop}
      style={{
        position: "fixed",
        bottom: "30px",
        right: "30px",
        width: "55px",
        height: "55px",
        borderRadius: "50%",
        backgroundColor: "#007bff",
        color: "white",
        border: "none",
        cursor: "pointer",
        boxShadow: "0 4px 10px rgba(0,0,0,0.3)",
        zIndex: 1000,
        transition: "transform 0.3s ease",
      }}
      onMouseEnter={(e) => (e.currentTarget.style.transform = "scale(1.1)")}
      onMouseLeave={(e) => (e.currentTarget.style.transform = "scale(1)")}
    >
      <FaArrowUp size={20} />
    </button>
  );
};

const Landing = () => {
    const navigate = useNavigate();
    navigate("/login-area")
  return (
    <>
      <Header />
      <MainSlider />
      <AppDownloadSection />
      <FeatureSection />
      <DigitalSavingsSection />
      <MeshSection />
      <MobileAppSection />
      <ReachUsSection />
      <TestimonialsSection />
      <FaqSection />
      <Footer />
      <SideButtons />
      <ScrollToTop />
    </>
  );
};

export default Landing;
